.result-set-printer {
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 100%;
    background-color: #15181e;
    color: #c4c4c4;
    font-size: large;
    font-weight: 700;
    overflow: auto;
}

.result-set-printer pre {
    line-height: 1.5;
    margin-left: 5%;
    margin-top: 5%;
    color: #c4c4c4;
    font-size: large;
    font-weight: bold;
    cursor: default;
}